:root {
  /* typography */
  --font-weight-light: 300;
  --font-weight-medium: 500;
  --font-size-100: 0.6875rem;
  --font-size-200: 0.75rem;
  --font-size-300: 0.875rem;
  --font-size-400: 0.9375rem;
  --font-size-500: 1.25rem;

  /* color */
  --gray-100-hsl: 223deg 30% 9%;
  --gray-100: hsl(var(--gray-100-hsl));
  --gray-100-50pc: hsl(var(--gray-100-hsl) / 0.5);
  --gray-200-hsl: 223deg 36% 14%;
  --gray-200: hsl(var(--gray-200-hsl));
  --gray-300-hsl: 223deg 23% 46%;
  --gray-300: hsl(var(--gray-300-hsl));
  --gray-400-hsl: 223deg 0% 100%;
  --gray-400: hsl(var(--gray-400-hsl));
  --gray-400-75pc: hsl(var(--gray-400-hsl) / 0.75);
  --gray-400-25pc: hsl(var(--gray-400-hsl) / 0.25);
  --red-300: hsl(0deg 97% 63%);

  /* space */
  --space-xs-xs: clamp(0.75rem, 5.2vw - 0.7rem, 1.25rem);
  --space-s-s: clamp(1rem, 0.8vw + 0.75rem, 1.5rem);
  --space-s-m: clamp(1rem, 1.6vw + 0.5rem, 2rem);
  --space-s-l: clamp(1rem, 2.4vw + 0.25rem, 2.5rem);
  --space-m-m: clamp(1.5rem, 0.8vw + 1.25rem, 2rem);
  --space-m-l: clamp(1.5rem, 1.6vw + 1rem, 2.5rem);
  --space-m-xl: clamp(1.5rem, 4vw + 0.25rem, 4rem);
}

@media screen and (min-width: 768px) {
  :root {
    --font-size-100: 0.8125rem;
    --font-size-200: 0.9375rem;
    --font-size-300: 1.125rem;
    --font-size-400: 1.5rem;
    --font-size-500: 2rem;
  }
}

body {
  font-family: "Outfit", sans-serif;
  background-color: var(--gray-100);
  color: var(--gray-400);
  font-weight: var(--font-weight-light);
  overflow: hidden;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}
